import React, {useEffect, useState} from 'react';
import EfPic from "../../img/salma/ef.jpg";
import UdemyPic from "../../img/salma/udemy.png";
import ImamPic from "../../img/salma/imam.png"
import RiyadhChamber from "../../img/salma/RiyadhChamber.jpg"
import "./Education.css"
import Slide from "@material-ui/core/Slide";
import AlertDialog from "./AlertDialog";
import CF3 from "./certificate/CF3.jpg";
import CF2 from "./certificate/CF2-1.png";
import CF1 from "./certificate/Ef-1.jpg";
import CF from "./certificate/CF-1.png";


export default function Education() {
    let [checked,setChecked]=useState(false);
    useEffect(() => {
        window.addEventListener('scroll',()=>{
            if(window.scrollY>1430){
                setChecked(true);
            }
        })
    })

    return(
       <div  className="educationSection  container-fluid">
            <div className="row">
                <div className="col-lg-12">
                    <h1 className="text-center pb-5"> Education : </h1>
                    <ul className="expLine">
                        <li>
                            <Slide direction="right" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                            <div className="expLine-image">
                                <img className="rounded-circle img-fluid" src={ImamPic} alt=""/>
                            </div>
                            </Slide>
                            <Slide direction="right" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                                <div className="expLine-panel">
                                    <div  className="expLine-heading pt-4">
                                        <h4>2015 / 1 - 2019 / 9</h4>
                                        <h4 className="subheading">Imam Muhammad bin Saud Islamic University -  Finance and Investment</h4>
                                        <h5> Bachelor's Degree - GPA (4.77 from 5)</h5>
                                    </div>
                                   <AlertDialog imgSrc={CF3} />
                                </div>
                            </Slide>
                        </li>


                        <li className="expLine-inverted">
                            <Slide direction="left" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                            <div className="expLine-image">
                                <img className="rounded-circle img-fluid mt-lg-3 mt-s-2 "  src={RiyadhChamber} alt=""/>
                            </div>
                            </Slide>
                            <Slide direction="left" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                            <div className="expLine-panel">
                                <div className="expLine-heading">
                                    <h4>2019 / 7</h4>
                                    <h4 className="subheading">Risk Management</h4>
                                    <h5>Training course From Riyadh Chamber (20 hours)</h5>
                                </div>
                                <AlertDialog imgSrc={CF2} />
                            </div>
                            </Slide>
                        </li>


                        <li>
                        <Slide direction="right" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                            <div className="expLine-image ">
                                <img className="rounded-circle img-fluid mt-3" src={EfPic} alt=""/>
                            </div>
                        </Slide>
                        <Slide direction="right" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                            <div className="expLine-panel">
                                <div  className="expLine-heading pt-4">
                                    <h4>2019 / 10 - Present</h4>
                                    <h4 className="subheading">General English </h4>
                                    <h5> Online courses from EF (per day 4 hours) </h5>
                                </div>
                                <AlertDialog imgSrc={CF1} />
                            </div>
                        </Slide>
                    </li>

                        <li className="expLine-inverted">
                            <Slide direction="left" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                                <div className="expLine-image">
                                    <img className="rounded-circle img-fluid" src={UdemyPic} alt=""/>
                                </div>
                            </Slide>
                            <Slide direction="left" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                                <div className="expLine-panel">
                                    <div className="expLine-heading">
                                        <h4>2020 / 7</h4>
                                        <h4 className="subheading">FInancial Analysts  </h4>
                                        <h5>Online course From Udemy (20 hours)</h5>
                                    </div>
                                    <AlertDialog imgSrc={CF} />
                                </div>
                            </Slide>
                        </li>


                    </ul>
                </div>
            </div>

        </div>

    );
}

