import React, {useEffect, useState} from 'react';
import Paper from "@material-ui/core/Paper";
import "./About.css"
import Divider from "@material-ui/core/Divider";
import bk3 from "../../img/salma/5240_burned.png";
import IconButton from "@material-ui/core/IconButton";
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import makeStyles from "@material-ui/core/styles/makeStyles";
import Slide from "@material-ui/core/Slide";


const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
            marginTop:20

        },
    },
}));

export default function About() {
    let [checked,setChecked]=useState(false);

    useEffect(() => {
        window.addEventListener('scroll',()=>{
            if(window.scrollY>630){
                setChecked(true);
            }
        })
    })


    const classes = useStyles();

    return(
          <div className="aboutSection container-fluid ">
              <div className="row " id="rowAbout">
                  <div className="col-lg-6 col-sm-10 ">
                      <Slide direction="right" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                      <Paper elevation={3}  className="aboutPaper p-3  pr-0 ml-md-5 m-sm-3 mr-lg-0">
                          <div className="aboutText pt-3">
                          <h1>ABOUT ME:</h1>
                          <p className="text-justify mt-3">
                              I'm salma Abdullah Almazyad. I’m Saudi. I am 25 years old.
                              I'm an ambitious person looking for the best by improving my skills experiences.
                              I'm interested in CFA certification and I wish get it after a few years. I will exploit all my academic and practical
                              experiences in serving and developing the organization I work for.
                          </p>
                              <p className="text-justify mt-3">
                                  In the University, I worked on several projects including Evaluation of United
                                  Electronics Company Project,The Virtual Stock Prtfolio Project, Feasibility Study for
                                  a Virtual Project, and Analysis of the Financial Statement of the United of Wire
                                  Factories Company Project By using Excel program .
                              </p>
                          </div>
                          <div className={classes.root}>
                              <IconButton  href="mailto:Salmaabdullahh95@gmail.com"  target="_blank" style={{ marginRight:0}}>
                                  <MailOutlineIcon  fontSize="large" style={{color:"#ca0505"}}/>
                              </IconButton>
                              <IconButton  href="http://linkedin.com/in/salma-almazyad-3148a617b"  target="_blank"  style={{ marginRight:0}}>
                                <LinkedInIcon   fontSize="large"  color="primary" style={{ paddingRight:0}}/>
                              </IconButton>
                              <IconButton href="https://iwtsp.com/966560354355"  target="_blank" style={{ marginRight:0}} >
                                  <WhatsAppIcon  fontSize="large" style={{color:"green"}} />
                              </IconButton>
                          </div>
                      </Paper>
                      </Slide>
                  </div>
                  <Slide direction="up" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                  <div className="col-2 p-0  d-none d-lg-block ">
                      <Divider className="divider"/>
                  </div>
                  </Slide>
                  <Slide direction="left" in={checked} timeout={1000} mountOnEnter unmountOnExit>
                  <div className="col-4 p-0 d-none d-lg-block">
                      <img src={bk3} style={{width:"30vw"}} className="float-right mb-5 " alt=""/>
                  </div>
                  </Slide>
              </div>

        </div>

    );
}