import React, {useEffect, useState} from 'react';
import "./Skills.css";
import MicrosoftLogo from "../../img/salma/Microsoft.png";
import EviewsLogo from "../../img/salma/ve.png";
import FeasLogo from "../../img/salma/feas.svg";
import FinanLogo from "../../img/salma/financial.svg";
import TimeLogo from "../../img/salma/clock.svg";
import PresLogo from "../../img/salma/presentation.svg";
import project1 from "../../img/salma/analytics.svg";
import project2 from "../../img/salma/project.svg";
import project3 from "../../img/salma/folder.svg";
import Grow from "@material-ui/core/Grow";
import {makeStyles} from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";



const useStyles = makeStyles({
    root: {
        maxWidth: 300,
        margin:20,
        height: 300
    },
    media: {
        height: 140,
        backgroundSize: 100,

    },
    EmcMedia:{
        height: 150,
        backgroundSize: 100,
        backgroundPosition:"center"
    },
    CrawlerMedia:{
        height: 150,
        backgroundSize: 350,
        backgroundPosition:"center"
    },
    TodoMedia:{
        height: 150,
        width:300,
        backgroundSize: 350,
        backgroundPosition:"center"
    }
});

export default function Skills() {
    const classes = useStyles();
    let [active,setActive]=useState(1);

    const handleColor = (num) => {
        setActive(num);
    }
    let [checked,setChecked]=useState(false);
    useEffect(() => {
        window.addEventListener('scroll',()=>{
            if(window.scrollY>2800){
                setChecked(true);
            }
        })
    })

    const tooltipS=(
            <div >
                <div  className="mySkills">
                        <div  className=" d-md-inline-block  d-sm-block">
                            <Grow in={checked}>
                            <h1 className="mb-3">Skills: </h1>
                            </Grow>
                            <ul >

                                <li >
                                    <div className=" skill d-md-inline-block  d-sm-block">
                                        <Grow in={checked} timeout={1200}>
                                               <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${FeasLogo})`}}
                                                     onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                        <div className="mt-md-3 mt-sm-0">
                                            Feasibility <br/>study
                                        </div>
                                    </div>
                                </li>
                                <li className="ml-3" >
                                    <div className=" skill d-md-inline-block  d-sm-block ">
                                        <Grow in={checked}>
                                                <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${MicrosoftLogo})`}}
                                                      onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                    </div>
                                    <div>
                                        Microsoft <br/>
                                        Office
                                    </div>
                                </li>

                                <li className="ml-3" >
                                    <div className="skill d-md-inline-block  d-sm-block">
                                        <Grow in={checked} timeout={1100} >
                                               <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${EviewsLogo})`, height:87 }}
                                                     onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                        <div>
                                           EViews
                                        </div>
                                    </div>
                                </li>

                                <li >
                                    <div className=" skill d-md-inline-block d-sm-block ">
                                        <Grow in={checked} timeout={1300}>
                                                <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${FinanLogo})`}}
                                                      onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                        <div className="mt-md-3 mt-sm-0">
                                            Financial <br/> analyst
                                        </div>
                                    </div>
                                </li>
                                <li >
                                    <div className=" skill d-md-inline-block  d-sm-block" >
                                        <Grow in={checked} timeout={1400} >
                                                <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${TimeLogo})`}}
                                                      onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                        <div className="mt-md-3 mt-sm-0">
                                            Time <br/> management
                                        </div>
                                    </div>
                                </li>
                                <li >
                                    <div className=" skill d-md-inline-block  d-sm-block" >
                                        <Grow in={checked} timeout={1500} >
                                                <span className="flutterIcon skillIcon" style={{backgroundImage: `url(${PresLogo})`}}
                                                      onMouseEnter={() => handleColor(7)}/>
                                        </Grow>
                                        <div className="mt-md-3 mt-sm-0">
                                            Making <br/> presentation
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                </div>
            </div>
    )

    const cardProjects= (
        <div className=" d-lg-inline-block ">
            <br/>
            <Grow in={checked}>
                <div>
                <h1 className="mb-3 d-inline">Projects : </h1>
                </div>
             </Grow>
            <Grow in={checked} timeout={1000}>
            <Card className={`${classes.root} myCard  `}>
                <CardActionArea className={` myCard  `} href="https://drive.google.com/drive/folders/1GOSZV1Hxf0wxXhloep676IypkpCZnW2E?usp=sharing" target="_blank">
                    <CardMedia
                        className={classes.EmcMedia}
                        image={project1}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2">
                            Financial Analyst
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p" className="text-justify">
                            This project I did in the Financial Analysis course From Udemy.
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
            </Grow>

            <Grow in={checked} timeout={1200}>
                <Card className={`${classes.root} myCard`} >
                    <CardActionArea  className={` myCard  `} href="https://drive.google.com/file/d/13PvfjWwjlWC7d4YNd9_SS8VRMaabAjHZ/view?usp=sharing" target="_blank">
                        <CardMedia
                            className={classes.EmcMedia}
                            image={project2}
                        />
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                Evaluation project
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                 This project is about Corporate evaluation,<br/>
                                I choose EXTRA company to evaluate them, I used Income Approach, Market Approach, and Cost Approach.
                            </Typography>
                        </CardContent>
                    </CardActionArea>
                </Card>
            </Grow>

            <Grow in={checked} timeout={1100}>
            <Card className={`${classes.root} myCard`}>
                <CardActionArea  className={` myCard  `} href="https://drive.google.com/drive/folders/1H_M5yvS0uXKPjQN4tstof6nS6qJa1-K6?usp=sharing" target="_blank">
                    <CardMedia
                        className={classes.EmcMedia}
                        image={project3}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2">
                            All Projects
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p">
                            Here is all of my projects  related to Finance and Investment.
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
            </Grow>


        </div>
    )

    return(
        <div className="skillsSection ">
            <div className="container-fluid ">
                <div className="row d-block">
                {tooltipS}
                </div>

                <div className="myProjects mt-5">
                    {/*<Projects checked={checked} />*/}
                    {cardProjects}
                </div>
            </div>
        </div>
    );
}